/* make variables */
:root {
  --lightgray: #D9D9D9;
  --gray: #9c9c9c;
  --orange: #f48915;
  --darkGrey: #464D53;
  --caloryCard: #656565;
  --planCard: linear-gradient(210.41deg, #fa5042 1.14%, #ffa739 100.75%);
  --appColor:  #3c3f45;
}

.App {
  background-image: url(data:image/png;base64,);
  mix-blend-mode: overlay;
  background-color: var(--appColor);
  display: flex;
  flex-direction: column;
  gap: 6rem;
  overflow: hidden;
}
::-webkit-scrollbar{
  display: none;
}
.button{
  font-weight: 500;
  padding: 0.6rem 1.4rem;
  color: white;
  background: blue;
  border: none;
  border-radius: 4px;
  transition: all 300ms ease-in-out;
  height: 2rem;
  width: 6rem;
}
.link{
  text-decoration: none;
}
.stroke-text{
  color: transparent;
  font-family: Arial, Helvetica, sans-serif;
  -webkit-text-stroke-width: 1px;
  -webkit-text-stroke-color: white;
}
.btn{
  background: white;
  padding: 0.5rem;
  font-weight: bold;
  border: 4px solid transparent;
  transition: 300ms all;
  display: flex;
  align-items: center;
  justify-content: center;
}
.btn:hover{
  cursor: pointer;
}
.blur{
  background: #ef890c;
  position: absolute;
  border-radius: 50%;
  filter: blur(180px);
  z-index: -9;
}
@media screen and (max-width: 768px) {
  .home{
    flex-direction: column;

  }
  .hero-blur{
    width: 14rem;
  }
  .the-best-add{
    margin-top: 0;
    font-size: small;
    align-self: center;
    transform: scale(0.8);

  }
  .hero-text{
    font-size: xx-large;
    align-items: center;
    justify-content: center;

  }
  .hero-text>div:nth-child(3){
    font-size: small;
    font-weight: 200;
    letter-spacing: 1px;

  }
  .hero-button{
    justify-content: center;
  }
  .figures>div> span :nth-last-of-type(1){
    font-size: large;
  }
  .figures>div> span :nth-last-of-type(2){
font-size: small;
  }
  .right-h{
    position: relative;
    background: none;

  }
  .heart-rate{
    left: 1rem;
    top: 2rem;

  }
  .calories{
    position: relative;
    top: 5rem;
    left: 2rem;
  }
  .calories>img{
    width: 2rem;
  }
  .calories>div>:nth-child(2){
    color: white;
    font-size: 1rem;

  }
  .hero-image{
    position: absolute;
    width: 15rem;
    left: 7rem;
    top: 4rem;
    align-self: center;

  }

}
@media screen and (max-width: 768px){
  .programs-header{
    flex-direction: column;
    gap: 1rem;
    font-size: x-large;
    align-items: center;
    justify-content: center;
    margin-top: 2rem;

  }
  .programs-catageries{
    flex-direction: column;

  }
}
